<template>
  <div class="main">
    <el-form :inline="true" :model="formInline">
      <el-form-item label="To:">
        <el-select v-model="formInline.to">
          <el-option label="火龙果叉烧肠粉" value="火龙果叉烧肠粉"></el-option>
          <el-option label="而今听雨" value="而今听雨"></el-option>
          <el-option label="黑堂隐月" value="黑堂隐月"></el-option>
          <el-option label="typeomicron" value="typeomicron"></el-option>
          <el-option label="枞枞那年" value="枞枞那年"></el-option>
          <el-option label="fluoria" value="fluoria"></el-option>
          <el-option label="万世常青" value="万世常青"></el-option>
          <el-option label="duebdusej" value="duebdusej"></el-option>
          <el-option label="yumemichan" value="yumemichan"></el-option>
          <el-option label="Awilix" value="Awilix"></el-option>
          <el-option label="不存在的竹渊" value="不存在的竹渊"></el-option>
          <el-option label="71和EU" value="71和EU"></el-option>
          <el-option label="太阳咸鱼" value="太阳咸鱼"></el-option>
          <el-option label="林猫音" value="林猫音"></el-option>
          <el-option label="-÷" value="-÷"></el-option>
          <el-option label="EugeneGen" value="EugeneGen"></el-option>
          <el-option label="wcz214" value="wcz214"></el-option>
          <el-option label="GodisaDeadII" value="GodisaDeadII"></el-option>
          <el-option label="fs & 海原" value="fs & 海原"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-input
          placeholder="搜索内容"
          prefix-icon="el-icon-search"
          v-model="formInline.search"
          clearable
        />
      </el-form-item>
    </el-form>
    <div v-for="item in filteredLetter" :key="item.detail">
      <Letter :letterDetail="item"></Letter>
    </div>
    <el-empty v-if="filteredLetter == ''" />
  </div>
</template>

<script>
import Letter from "@/components/posts/Letter.vue";
// 私信数据
import letterDetail from "@/assets/letters";
export default {
  components: {
    Letter,
  },
  data() {
    return {
      letterDetail,
      // 搜索条件
      formInline: {
        to: "火龙果叉烧肠粉",
        search: "",
      },
    };
  },
  computed: {
    // 数据筛选
    filteredLetter() {
      let filteredLetter = this.letterDetail;
      filteredLetter = filteredLetter.filter(
        (item) => item.to == this.formInline.to
      );
      // 内容搜索
      if (this.search != "") {
        filteredLetter = filteredLetter.filter((item) =>
          item.detail.includes(this.formInline.search)
        );
      }
      return filteredLetter;
    },
  },
};
</script>

<style lang="less" scoped>
.el-form {
  margin: 2vh auto;
}
</style>